import React from "react"
import {
  FaSlack,
  FaLinkedin,
  FaWhatsappSquare,
  FaMeetup,
  FaMedium,
} from "react-icons/fa"

import {
  BsFileEarmarkPerson
} from "react-icons/bs"

const data = [
  {
    id: 1,
    icon: <FaLinkedin className="social-icon" aria-label="LinkedIn"></FaLinkedin>,
    url: "https://www.linkedin.com/in/natanael-mota-b15b653a/",
  },
  {
    id: 2,
    icon: <FaSlack className="social-icon" aria-label="Slack"></FaSlack>,
    url: "https://mindtheproduct.slack.com/team/U04Q9HBB65C",
  },
  {
    id: 3,
    icon: <BsFileEarmarkPerson className="social-icon" aria-label="CV"></BsFileEarmarkPerson>,
    url: "https://github.com/NatMota/natmota.github.io/blob/master/Natanael%20-%20CV.pdf",
  },
  // {
  //   id: 4,
  //   icon: <FaKaggle className="social-icon" aria-label="Kaggle"></FaKaggle>,
  //   url: "https://www.kaggle.com/natmota/competitions",
  // },
  // {
  //   id: 5,
  //   icon: <FaMeetup className="social-icon" aria-label="Meetup"></FaMeetup>,
  //   url: "https://www.meetup.com/members/243265736/",
  // }
]
const links = data.map(link => {
  return (
    <li key={link.id}>
      <a href={link.url} className="social-link">
        {link.icon}
      </a>
    </li>
  )
})

export default ({ styleClass }) => {
  return (
    <ul className={`social-links ${styleClass ? styleClass : ""}`}>{links}</ul>
  )
}
